@font-face {
  font-family: "Testments";
  src: local("Testmets"),
    url(./assets/fonts/testmentRegular.ttf) format("truetype");
}
@font-face {
  font-family: "Amulet";
  src: local("Amulet"), url(./assets/fonts/amulet.ttf) format("truetype");
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.customModal {
  color: antiquewhite;
  background: #1a1f22 !important;
}
